import React, { useState } from "react";
import sound from "../sound/pop-cat-original-meme_3ObdYkj.mp3";
import PopCat from "../img/pop-cat.png";
import unPopCat from "../img/unpop-cat.png";
import styles from "./Popcat.module.css";
import {
  BrowserView,
  MobileView,
  isBrowser,
  isMobile,
} from "react-device-detect";
import { hasSelectionSupport } from "@testing-library/user-event/dist/utils";
function Popcat() {
  const [imageSRC, changeImage] = useState(unPopCat);
  const audio = new Audio(sound);
  const popDown = () => {
    changeImage(PopCat);
    audio.play();
  };
  const popUp = () => {
    changeImage(unPopCat);
    audio.pause();
    audio.currentTime = 0;
  };
  const popTouch = () => {
    changeImage(PopCat);
    audio.play();
    sleep(500);
    changeImage(unPopCat);
  };
  const sleep = (ms) => {
    return new Promise((resolve) => setTimeout(resolve, ms));
  };
  return (
    <div className={styles.container}>
      <BrowserView>
        <img
          className={styles.popcat}
          src={imageSRC}
          onMouseDown={popDown}
          onMouseUp={popUp}
        />
      </BrowserView>
      <MobileView>
        <img
          className={styles.popcat}
          src={imageSRC}
          onTouchStart={popDown}
          onTouch={popTouch}
          onTouchEnd={popUp}
        />
      </MobileView>
    </div>
  );
}

export default Popcat;
